*,
*::after,
*::before {
    margin: 0;
    padding: 0; 
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Rubik', sans-serif;
    box-sizing: border-box;
    font-size: 1.6rem;
}

button {
    background: none;
}

i {
    cursor: pointer;
}